body {
  margin: auto;
  padding: 0 0.5em;
  max-width: 45em;
  font-family: sans-serif;
  font-size: 1rem;
  line-height: 1.8;

  header {
    h1 {
      margin-bottom: 0.5ex;
    }

    time {
      display: block;
      float: right;
    }

    &:after {
      clear: both;
      content: "";
      display: block;
    }
  }

  img {
    border: thin double #aaa;
    margin: 0 1ex;
  }
  .thumbnail {
    max-width: 75%;
    max-height: 30em;
  }

  pre,
  code {
    background-color: #f0f0f0;
    border-radius: 0.2em;
  }

  pre {
    padding: 0.3em;
  }

  code {
    padding: 0;
  }

  nav {
    margin-top: 2em;
    clear: both;

    .send-pullreq {
      font-size: small;
      float: right;
    }
  }

  article > footer {
    border-top: thin solid gray;

    .changelog {
      h2 {
        font-size: 100%;
      }
    }
  }

  dt:not(:first-child) {
    margin-top: 1em;
  }
}
